nav ul{
  list-style: none;
  text-align: center;
  list-style-type: none;
  margin: 0;
  padding: 0;
  width: 100px;
  background-color: #C2B280;
  position:absolute;
  width: 99.5%;
}

nav ul li a {
  float: left;
  width: 18.3%;
  font-size: 25px;
  padding: 14px 16px;
  text-decoration: none;
}

nav ul li a:hover:not(.active) {
  background-color: black;
  color: white;
}

body{
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  height: 1000px;
  background: linear-gradient(180deg, #1677C5 0%, #1A224C 31%, #0D1126 75%);
  color: azure;
}

h1 {
  font-weight: 300;
  font-size: 40px;
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  color: #EDB32B;
}

h2 {
  margin: 100px;
  font-size: 35px;
}

/* to change the color of unvisited links */
a:link{
color: darkblue;
}
a:visited{
color: darkblue;
}

